:host {
  display: block;
  position: relative;
  height: 2rem;
  width: 2rem;
  margin-block-start: 0.3125rem;
  margin-inline-end: 0.3125rem;
  margin-inline-start: auto;
}
:host .hide {
  visibility: collapse;
}
:host ::slotted(ix-dropdown) {
  cursor: default;
}